<script setup lang="ts">
import { ref, computed, onMounted, onUnmounted, watch, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import axios from 'axios'
import Card from '@/components/ui/Card.vue'
import CardHeader from '@/components/ui/CardHeader.vue'
import CardTitle from '@/components/ui/CardTitle.vue'
import CardDescription from '@/components/ui/CardDescription.vue'
import CardContent from '@/components/ui/CardContent.vue'
import Button from '@/components/ui/Button.vue'
import Input from '@/components/ui/Input.vue'
import Label from '@/components/ui/Label.vue'
import Textarea from '@/components/ui/Textarea.vue'
import Select from '@/components/ui/Select.vue'
import Checkbox from '@/components/ui/Checkbox.vue'
import Badge from '@/components/ui/Badge.vue'
import Progress from '@/components/ui/Progress.vue'
import Icons from '@/components/ui/Icons.vue'
import DevPhaseNavigation from '@/components/DevPhaseNavigation.vue'
import { rfpUseToast } from '@/composables/rfpUseToast'
import { useRFPStore } from '@/store'

// API base URL - use the Django backend endpoints
const API_BASE_URL = 'http://localhost:8000/api/v1'

interface EvaluationCriteria {
  id: string
  name: string
  description: string
  weight: number
  isVeto: boolean
}


const { success, error } = rfpUseToast()
const rfpStore = useRFPStore()
const router = useRouter()

const showDraftRecovery = ref(false)
const lastSaved = ref<Date | null>(null)
const isAutoSaving = ref(false)
const isSubmitting = ref(false)
const isGeneratingDocument = ref(false)

const formData = ref({
  // Basic Information
  rfpNumber: '',
  title: '',
  description: '',
  type: '',
  category: '',
  
  // Financial Information
  estimatedValue: '',
  currency: 'USD',
  budgetMin: '',
  budgetMax: '',
  
  // Timeline Information
  issueDate: '',
  deadline: '',
  evaluationPeriodEnd: '',
  timeline: '',
  
  // Evaluation & Process
  evaluationMethod: 'weighted_scoring',
  criticalityLevel: 'medium',
  
  // Scope & Requirements
  geographicalScope: '',
  complianceRequirements: '',
  
  // Additional Options
  allowLateSubmissions: false,
  autoPublish: false,
})

const criteria = ref<EvaluationCriteria[]>([
  {
    id: '1',
    name: 'Technical Capability',
    description: 'Vendor\'s technical expertise and infrastructure',
    weight: 30,
    isVeto: true,
  },
  {
    id: '2',
    name: 'Cost Effectiveness',
    description: 'Value for money and pricing structure',
    weight: 25,
    isVeto: false,
  },
  {
    id: '3',
    name: 'Experience & References',
    description: 'Previous experience and client references',
    weight: 20,
    isVeto: true,
  },
  {
    id: '4',
    name: 'Implementation Timeline',
    description: 'Proposed timeline and delivery schedule',
    weight: 15,
    isVeto: false,
  },
  {
    id: '5',
    name: 'Support & Maintenance',
    description: 'Ongoing support and maintenance capabilities',
    weight: 10,
    isVeto: false,
  },
])


const totalWeight = computed(() => {
  return criteria.value.reduce((sum, criterion) => sum + criterion.weight, 0)
})

// Track weight adjustments for notifications
const weightAdjustmentHistory = ref<Array<{criterion: string, oldWeight: number, newWeight: number}>>([])
const showWeightAdjustmentNotification = ref(false)


const isFormValid = computed(() => {
  // Check required fields
  if (!formData.value.rfpNumber || !formData.value.title || !formData.value.description || 
      !formData.value.type || !formData.value.deadline || !formData.value.issueDate) {
    return false
  }
  
  // Check evaluation criteria
  if (criteria.value.length === 0 || totalWeight.value !== 100) {
    return false
  }
  
  return true
})

